<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--   -->
    <el-card>
      <!--   -->
      <el-row>
        <el-col :span="9">
          <el-input placeholder="" v-model="queryInfo.query" clearable
                    @clear="getOrdersList" @keyup.enter.native="getOrdersList">
            <el-button slot="append" icon="el-icon-search" @click="getOrdersList"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="ordersList" border stripe>
        <el-table-column type="index" align="center"></el-table-column>
        <el-table-column label="" prop="order_number"></el-table-column>
        <el-table-column label="" prop="order_price" width="80px" align="center"></el-table-column>
        <el-table-column label="" prop="pay_status" width="90px" align="center">
          <template v-slot="scope">
            <el-tag v-if="scope.row.pay_status === '1'" type="success" ></el-tag>
            <el-tag v-else type="danger" ></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send" width="80px" align="center"></el-table-column>
        <el-table-column label="" width="180px" prop="create_time">
          <template v-slot="scope">
            {{scope.row.create_time | timeFormater}}
          </template>
        </el-table-column>
        <el-table-column label="" width="150px" align="center">
          <template v-slot="">
            <el-button size="mini" icon="el-icon-edit" type="primary" @click="editAdress"></el-button>
            <el-button size="mini" icon="el-icon-location" type="success" @click="showProgressBox"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>      
    <!--  -->
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editAdressDialogVisible"
      width="600px"
      @close="editAdressDialogclosed">
      <!--  -->
      <el-form :model="editAdressForm" :rules="editAdressFormRules" ref="editAdressFormRef" label-width="134px">
        <el-form-item label="/" prop="province">
          <el-cascader
            v-model="editAdressForm.province"
            :options="cityData"
            :props="{ expandTrigger: 'hover' }">
          </el-cascader>
        </el-form-item>
        <el-form-item label="" prop="adress">
          <el-input v-model="editAdressForm.adress"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editAdressDialogVisible = false"> </el-button>
        <el-button type="primary" @click="saveEditAdress"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="progressDialogVisible"
      width="600px">
      <!--  -->
      <el-timeline :reverse="false">
        <el-timeline-item
          v-for="(activity, index) in progressInfo"
          :key="index"
          :timestamp="activity.time">
          {{ activity.context }}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
import cityData from './citydata'

export default {
  data() {
    return {
      //  
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      //  
      total: 0,
      //  
      ordersList: [],
      //  
      editAdressDialogVisible: false,
      //  
      editAdressForm: {
        adress: '',
        province: ''
      },
      //  
      editAdressFormRules: {
        province: [
          { required: true, message: '/', trigger: 'blur' }
        ],
        adress: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      //  
      cityData,
      //  
      progressInfo: [],
      //  
      progressDialogVisible: false
    }
  },
  created() {
    this.getOrdersList()
  },
  methods: {
    // 
    async getOrdersList() {
      const {data: res} = await this.$http.get('orders', { params: this.queryInfo })
      // console.log(res);
      if(res.meta.status !== 200) {
        return this.$message.error({
          message: '!',
          duration: 1500
        })
      }
      this.total = res.data.total
      this.ordersList = res.data.goods
      this.$message.success({
        message: '',
        duration: 1500
      })
    },
    // pagesize
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrdersList()
    },
    // currentpage
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getOrdersList()
    },
    // 
    editAdress() {
      // 
      this.editAdressDialogVisible = true
    },
    // 
    editAdressDialogclosed() {
      // 
      this.$refs.editAdressFormRef.resetFields()
    },
    // 
    saveEditAdress() {
      this.$refs.editAdressFormRef.validate(valid => {
        if(!valid) return
        this.editAdressDialogVisible = false
      })
    },
    // 
    async showProgressBox() {
      const { data: res } = await this.$http.get('/kuaidi/1106975712662')
      if (res.meta.status !== 200) {
        return this.$message.error({
          message: '!',
          duration: 1500
        })
      }
      this.progressInfo = res.data
      this.progressDialogVisible = true
    }
  }
}
</script>

<style lang="less" scoped>
.el-input, .el-cascader {
  width: 350px;
}
</style>